.collapse-panel {
  padding: 15px;
  margin-bottom: 25px;
  border: 1px solid #9e9e9e;
  border-radius: 10px;
  font-size: 1.5em;
  -webkit-box-shadow: 3px 3px 5px #d4d4d4;
          box-shadow: 3px 3px 5px #d4d4d4;
  background: white;
}

.collapse-header {
  font-size: 22px;
  color: #9e9e9e;
  cursor: pointer;
}

.collapse-header > i {
  padding-right: 10px;
}

.collapse-body {
  padding: 0 27px;
}

.collapse-body li {
  background: #fda7a7;
  margin-bottom: 10px;
  border-radius: 10px;
  padding: 10px;
  color: #494949;
  list-style: none;
}
.collapse-body ul {
  padding-left: 0;
}

.collapse-body li > i {
  float: right;
  cursor: pointer;
}

.fade-leave {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
      transform: translateX(0);
}
.fade-leave.fade-leave-active {
  background: #fff;
  -webkit-transform: translateX(120%);
  -ms-transform: translateX(120%);
      transform: translateX(120%);
}



